    /*----------------------pedometer------------------------------*/
     $scope.mapView = function() {
        $scope.wayPoints = [];
        var onOptions = { maximumAge: 3000, timeout: 5000, enableHighAccuracy: true };
        if(navigator.geolocation){
            navigator.geolocation.getCurrentPosition(function(position){
                  $scope.locationPoint = [];
                        var posLat = parseFloat(position.coords.latitude);
                        var posLong = parseFloat(position.coords.longitude);
                        var getOrg = {
                            location : {
                                lat: posLat, 
                                lng: posLong
                            }       
                        };
                        $scope.wayPoints.push(getOrg);
                        console.log($scope.wayPoints);
                        $scope.origin = $scope.wayPoints[0].location;
                        $scope.destination = $scope.wayPoints[$scope.wayPoints.length-1].location; 
                        $scope.current = $scope.wayPoints[$scope.wayPoints.length-1].location;   

            },function(error){
                console.log(error);
            },onOptions);

            $scope.startTrigger = function(){
                $scope.wayPoints = [];
                navigator.geolocation.watchPosition(function(position){  
                        var posLat = parseFloat(position.coords.latitude);
                        var posLong = parseFloat(position.coords.longitude);
                        var getOrg = {
                            location : {
                                lat: posLat, 
                                lng: posLong
                            }       
                        };
                        $scope.wayPoints.push(getOrg);
                        console.log($scope.wayPoints);
                        //$scope.distance = distance($scope.locationPoint[0].latitude, $scope.locationPoint[0].longitude,$scope.locationPoint[$scope.locationPoint.length-1].latitude, $scope.locationPoint[$scope.locationPoint.length-1].longitude);
                        calsPerDay();
                },function(error){
                    console.log(error);
                },onOptions);
            };
        }   
        $scope.startPedometer = function() {
            $scope.hideStart = true;
            $scope.showPause = true;
            $scope.startPedo = true;
            $scope.startTrigger();
        }
        $scope.pausePedometer = function() {
            $scope.showPause = false;
            $scope.showResume = true;
        }
        $scope.resumePedometer = function() {
            $scope.showPause = true;
            $scope.showResume = false;

        }
        $scope.finishPedometer = function() {
            $scope.hideResult = true;
            $scope.showSummary = true;
        }
        function distance(lat1, lon1, lat2, lon2) {
            console.log('hi');
           var R = 6371; // km
          var dLat = (lat2 - lat1).toRad();
          var dLon = (lon2 - lon1).toRad(); 
          var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
                  Math.cos(lat1.toRad()) * Math.cos(lat2.toRad()) * 
                  Math.sin(dLon / 2) * Math.sin(dLon / 2); 
          var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a)); 
          var d = R * c;
          return d;
        }
        Number.prototype.toRad = function() {
          return this * Math.PI / 180;
        }
         function calsPerDay() {
          var age = 35;
          var height = 178;
          var weight = 78;
          var result = 0
          result = 66.47 + (13.75 * weight) + (5.0 * height - (6.75 * age));
          return Math.round( result );
        }
    }
